<template>
  <header class="header" :darkMode="darkMode">
    <div class="container">
      <div class="nav">
        <ul class="nav__menu">
          <li class="nav__item" v-for="link in linksArr" :key="link.id">
            <a class="nav__item-link" :href="`${link.path}`">{{
              link.title
            }}</a>
          </li>
        </ul>
        <div class="nav__switches">
          <label class="switch">
            <input type="checkbox" @click="darkModes()" />
            <span class="slider"></span>
          </label>
          <button class="nav__btn" @click="switchLang">
            {{ $i18n.locale }}
          </button>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  props: {
    linksArr: {
      type: Array,
      default: () => [],
    },
    darkMode: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    darkModes() {
      this.$emit("btn");
    },
    switchLang() {
      this.$i18n.locale == "ru"
        ? (this.$i18n.locale = "en")
        : (this.$i18n.locale = "ru");
    },
  },
};
</script>
